@font-face{
    font-family:Montserrat-Bold;
    src:url('../fonts/Montserrat-Bold.ttf') format('truetype');
}
@font-face{
    font-family:Montserrat-Regular;
    src:url('../fonts/Montserrat-Regular.ttf') format('truetype');
}
@font-face{
    font-family:Montserrat-Black;
    src:url('../fonts/Montserrat-Black.ttf') format('truetype');
}
@font-face{
    font-family:Montserrat-Italic;
    src:url('../fonts/Montserrat-Italic.ttf') format('truetype');
}
@font-face{
    font-family:Montserrat-Medium;
    src:url('../fonts/Montserrat-Medium.ttf') format('truetype');
}
@font-face{
    font-family:Montserrat-Light;
    src:url('../fonts/Montserrat-Light.ttf') format('truetype');
}
@font-face{
    font-family:Montserrat-SemiBold;
    src:url('../fonts/Montserrat-SemiBold.ttf') format('truetype');
}
@font-face{
    font-family:Montserrat-LightItalic;
    src:url('../fonts/Montserrat-LightItalic.ttf') format('truetype');
}
@media(min-width:1400px){
    .container{
        max-width:1200px;
    }
}

.fortunelanding{
    h1,h2,h3,h4,h5,h6,ul,li,p{
        padding: 0;
        margin: 0;
    }
    li{
     list-style: none;
    }
    img{
        max-width: 100%;
    }
    button{
        transition: .5s;
        &:hover{
            -webkit-box-shadow: -5px 15px 27px -13px rgba(0,0,0,0.75);
            -moz-box-shadow: -5px 15px 27px -13px rgba(0,0,0,0.75);
            box-shadow: -5px 15px 27px -13px rgba(0,0,0,0.75);
            transition: .5s;
        }
    }
   
    .bannersect{
        background: url('../images/bannerimg.jpg') no-repeat;
        background-size:cover;
        background-position: center center;
        .sectinside{
            padding: 32% 0;
            position: relative;
            h2{
                font-size: 40px;
                color: #ffffff;
                font-family:Montserrat-Light;
            }
            .orange{
                color: #ffc041;
                font-family:Montserrat-Black;
            }
            .whitebold{
                color: #fff;
                font-family:Montserrat-Black;
            }
            .spone{
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                width: 251px;
            }
        }
   
    }
    .formsection{
        .insideformsect{
            width: 90%;
            margin: -97px auto 0 auto;
            padding: 38px 4%;
            background: url(../images/bannerimgform.jpg) no-repeat;
            background-size: 100% 100%;
            opacity: .96;
            
            h2{
                font-size: 25px;
                color: #0376aa;
                font-family:Montserrat-Bold;
                text-align: center;
                margin-bottom: 15px;
            }
            .gridsect{
                display:grid;
                grid-template-columns: repeat(5,1fr);
                grid-gap: 24px;
                align-items: flex-end;
                @media(min-width:991px) and (max-width:1124px) {
                    grid-template-columns: repeat(3, 1fr);
                }
                @media(min-width:768px) and (max-width:991px) {
                    grid-template-columns: repeat(3, 1fr);
                }
                @media (max-width:768px) {
                    grid-template-columns: repeat(1, 1fr);
                }
                label{
                    font-size: 15px;
                    color: #065a86;
                    font-family:Montserrat-SemiBold;
                }
                .btnone{
                    font-size: 16px;
                    color: #ffffff;
                    font-family:Montserrat-Bold;
                    background: #009fe7;
                    border-radius:5px;
                    width: 100%;
                    padding: 11px 0;
                   border: 0;
                   outline: none !important;
                }
                .form-control{
                    padding: 11px 8px;
                    font-size: 15px;
                    color: #000000;
                    outline: none;
                    height: 44px;
                }
            }
        }
       
    }
    .heading{
        font-size: 36px;
        color: #00164d;
        text-align: center;
        font-family:Montserrat-Black;
    }
    .whyplansect{
        padding:90px 0;
        h4{
            font-size: 18px;
            color: #777777;
            font-family:Montserrat-Regular;
            text-align: center;
            margin: 15px 0 40px 0;
        }
        .col-lg-4{
            &:nth-child(2){
                border-right: 1px solid #ebebeb;
                border-left: 1px solid #ebebeb;
            }
        }
        .sectinside{
            
            padding: 13px 0 41px 0;
         
            
            .imgsect{
                width: 140px;
                height: 140px;
               border:10px solid #fff4f0;
               border-radius:50%;
               display: flex;
               align-items: center;
               justify-content: center;
               margin: 0 auto;
               position: relative;
               &::before{
                content: " ";
                position: absolute;
                z-index: -1;
                top: 5px;
                left: 5px;
                right: 5px;
                bottom: 5px;
                border: 5px solid #ff8f69;
                border-radius: 50%;
               }
               
            }
            p{
                font-size: 16px;
                color: #666666;
                font-family:Montserrat-Regular;
                text-align: center;
            }
            h5{
                font-size: 19px;
                color: #00164d;
                font-family:Montserrat-SemiBold;
                text-align: center;
                padding: 28px 0 12px 0;
            }
        }
       
    }
    .choosedessect{
        background: #edf3f9;
        padding: 110px 0 93px 0;
        .sectrepeatz{
            margin-top: 32px;
            border-radius:3px;
            overflow: hidden;
            position: relative;
            img{
                width: 100%;
            }
            &:hover{
                .overimg{
                    height: 100%;
                    padding: 38% 0 0 0;
                }
                .btntwo{
                    display: block !important;
                }
            }
            .overimg{
                position: absolute;
                width: 100%;
                height: 100%;
                background: rgba(9, 154, 224, 0.88);
                bottom: 0;
                height: 40px;
                transition: .5s ease;
                display: block;
               
                .btntwo{
                    font-size: 17px;
                    color: #ffffff;
                    font-family:Montserrat-SemiBold;
                    background: #ffb726;
                    padding: 16px 0;
                    width: 80%;
                    margin: 0 auto;
                    display: none;
                        
                }
                h4{
                    font-size: 18px;
                    color: #ffffff;
                    font-family:Montserrat-SemiBold;
                    text-align: center;
                    padding: 10px 0;
                    text-transform: uppercase;
                }
            }
        }
    }
    .sectionfortune{
        padding: 110px 0;
        background:url('../images/bannerimgtwo.jpg') no-repeat;
        background-size:cover;
        background-position:center;
        p{
            font-size: 16px;
            color: #555555;
            font-family:Montserrat-Regular;
            line-height: 28px;
            padding: 11px 0 33px 0;
        }
        .heading{
            text-align: left;
        }
        .ulone{
            li{
                font-size: 17px;
                color: #333333;
                font-family:Montserrat-SemiBold;
                position: relative;
                padding-left: 26px;
                margin-bottom: 18px;
                &::before{
                    content:"";
                    width: 25px;
                    height: 25px;
                    display: block;
                    position: absolute;
                    left: 0;
                    top: 0;
                    background: url('../images/listdotstyle.png') no-repeat;
                    background-position:center center;
                }
            }
        }
    }
    .customersect{
        padding: 110px 0;
        background: url('../images/bannerimgthree.jpg') no-repeat;
        background-size:cover;
        background-position: center  center;
        .slidersectone{
            .sectcoma{
                width: 74px;
                margin: 50px auto 14px auto;
            }
            p{
                font-size: 20px;
                color: #00164d;
                text-align: center;
                margin-bottom: 64px;
                font-family:Montserrat-LightItalic;
                width: 80%;
                margin: 0 auto;
            }
            h6{
                font-size: 20px;
                color: #00164d;
                font-family:Montserrat-Bold;
                text-align: center;
                margin: 27px 0;
            }
        }
        .slick-slide{
            outline: none;
        }
         .slick-dots {
            position: absolute;
            bottom: -62px;
            display: block;
            width: 100%;
            padding: 0;
            margin: 0;
            list-style: none;
            text-align: center;
        }
        .slick-dots li {
            position: relative;
            display: inline-block;
            width: 20px;
            height: 20px;
            margin: 0 5px;
            padding: 0;
            cursor: pointer;
        }
        .slick-dots li button {
            font-size: 0;
            line-height: 0;
            display: block;
            width: 20px;
            height: 20px;
            padding: 5px;
            cursor: pointer;
            color: transparent;
            border: 0;
            outline: none;
            background: transparent;
            &::before {
               
                font-family: 'slick';
                font-size: 44px;
                line-height: 20px;
                position: absolute;
                top: 0;
                left: 0;
                width: 20px;
                height: 20px;
                content: '•';
                text-align: center;
                opacity: .25;
                color: black;
                -webkit-font-smoothing: antialiased;
            }
        }
        .slick-dots li.slick-active button:before {
            opacity: .75;
            color: black;
        }
    }
    .exploresect{
        padding: 182px 0;
        background: url('../images/bannerimgfour.jpg') no-repeat;
        background-size: cover;
        background-position: 60%;
        h4{
            font-size: 40px;
            color: #ffffff;
            font-family:Montserrat-Black;
        }
        .btn{
            background: #ffb726;
            color: #ffffff;
            width: 239px;
            margin: 20px 0;
            font-family:Montserrat-SemiBold;
        }
    }

    footer{
        background: #0b2733;
        h6{
            font-size: 14px;
            color: #ffffff;
            font-family:Montserrat-SemiBold;
            padding: 20px 0;
            text-align: center;
        }
       
    }
    @media(max-width:768px){
        .heading {
            font-size: 26px;
        }
        .bannersect .sectinside h2{
            font-size: 29px;
            padding: 10px 0;
        }
         .bannersect .sectinside .spone{
            width: 175px;
        }
        .exploresect{
            background-position: 0;
            padding: 90px 0;
        }
        .exploresect h4 {
            font-size: 27px;
        }
        .bannersect{
            background:#019fe9;
           
        }
        .formsection .insideformsect {
            margin: 53px auto;
        }
        .bannersect .sectinside {
            padding: 25% 0 5% 0;
        }
       .whyplansect {
            padding: 40px 0;
        }
        .customersect .slidersectone p {
            font-size: 14px;
        }
    }
    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        .formsection .insideformsect .gridsect {
            display: flex;
            justify-content: space-around;

         }
         .formsection .insideformsect .gridsect .btn{
             width: 180px;
         }
    }

    
     
}
